n: center;\n    font-size: 18px;\n    height: 50px;\n    margin-bottom: 20px;\n    line-height: 50px;\n}\n.d-detail h2 {\n    margin: 0 !important;\n}\n.d-detail .fa-pos {\n    font-size: 16px;\n    color: #333;\n    margin-right: 10px;\n}\n.d-detail .dd-title {\n    font-size: 18px;\n    height: 50px;\n    line-height: 50px;\n    color: #666;\n}\n.d-detail .dd-list {\n    line-height: 26px;\n    margin-bottom: 20px;\n    position: relative;\n    border-top: 1px dashed #e8e8e8;\n    padding-top: 10px;\n}\n.d-detail .dd-list span {\n      display: inline-block;\n}\n.d-detail .dd-list .dd-index {\n      color: #666;\n      font-size: 14px;\n      margin-right: 10px;\n}\n.d-detail .dd-list .dd-name {\n      color: #333;\n}\n.d-detail .dd-list .dd-percent {\n      padding: 0 5px;\n      color: #fff;\n      font-size: 12px;\n      background: #ddd;\n      border-radius: 4px;\n}\n.d-detail .dd-list .dd-tag {\n      position: absolute;\n      right: 0;\n      top: 0;\n      color: #666;\n      cursor: pointer;\n}\n.d-detail .dd-list .dd-tag .fa {\n        position: relative;\n        top: -3px;\n        margin-left: 5px;\n}\n.d-detail .dd-list .dd-tag-dec {\n      position: absolute;\n      right: 0;\n      top: 28px;\n      /*display: none;*/\n      border-radius: 5px;\n      border: 1px solid #ddd;\n      padding: 10px 10px 2px;\n      line-height: 20px;\n      color: #666;\n}\n.d-detail .dd-detail, .d-detail .dd-more {\n    color: #888;\n    width: 800px;\n    line-height: 24px;\n}\n.d-detail .dd-more {\n    margin-top: 10px;\n    color: #333;\n}\n.d-detail .dd-self-percent {\n    width: 200px;\n    height: 40px;\n    border-radius: 4px;\n    border: 1px solid #ddd;\n    margin: 10px 0 20px;\n}\n.d-detail .dd-self-percent .fl {\n      float: left;\n      height: 40px;\n      line-height: 40px;\n      width: 80px;\n      text-align: center;\n      color: forestgreen;\n      font-size: 16px;\n      border-right: 1px solid #ddd;\n}\n.d-detail .dd-self-percent .fr {\n      